import React from 'react'

import Layout from "../components/layout"
import SEO from "../components/seo"
import Link from 'gatsby-link'
import {graphql} from "gatsby"

export default function Template({data}) {
    const {CoursePageQuery: course} = data;
    const {LessonListPageQuery: lessons} = data;

    return (
        <Layout>
            <SEO title={course.frontmatter.title} />
            <div className="courses-header pb-5 pt-5">
                <div className="container">
                    <h1 className="mb-0">{course.frontmatter.title}</h1>
                </div>
            </div>
            <div className="container mb-5 mt-4">
                <div dangerouslySetInnerHTML={{__html: course.html}} />
            </div>
            <div className="container mb-5 mt-5">
                <h2>Lessons</h2>
                {lessons.edges.map(lesson => (
                    <div key={lesson.node.id} className="video-container mb-3">
                        <Link to={lesson.node.frontmatter.path}>
                            <h4>{lesson.node.frontmatter.title}</h4>
                        </Link>
                        <div className="timing">{lesson.node.frontmatter.time}</div>
                        <div className="video mt-2">
                            <Link to={lesson.node.frontmatter.path} className="btn btn-outline-secondary">Learn More</Link>
                        </div>
                    </div>
                ))}
            </div>
        </Layout>
    )
}

export const pageQuery = graphql`
  query {
    LessonListPageQuery: allMarkdownRemark(
      filter: { 
        frontmatter: { 
          type: {eq: "lesson"},
          active: {eq: 1}
        }
      },
      sort: {
        fields: [frontmatter___path],
        order:DESC
      }
    ) {
      edges {
        node {
          id
          frontmatter {
            title
            path
            description
            price
            time
          }
        }
      }
    }
  
    CoursePageQuery: markdownRemark(frontmatter:  { path: {eq: "/courses/magento-2-beginner"}}) {
      html
      frontmatter {
        title
        path
        description
        price
        time
      }
    }
  }
`
